import axios from "axios";


var URLactual = window.location.host;
const baseUrl ="http://"+URLactual+"/api/profesor/" 


const abono = {};


abono.delete = async (id) => {
  const urlDelete = baseUrl+"/delete/"+id
  const res = await axios.delete(urlDelete)
  .then(response=> { return response.data })
  .catch(error =>{ return error })
  return res;
}


abono.update = async (state) => {

  const datapost = {
          
          legajo: state.legajo,
          nombre: state.nombre,
          apellido: state.apellido,
          especialidad: state.especialidad,
          fechaIngreso: state.fechaIngreso,
          fecha_nacimiento: state.fecha_nacimiento,
          idEscuela: {
          idEscuela: state.idEscuela.idEscuela,
          nombre: state.idEscuela.nombre
          },

  }
  console.log(datapost)
  const urlUpdate = baseUrl+"/update/"+state.id

  const res = await axios.put(urlUpdate,datapost)
  .then(response=>{ return response.data })
  .catch(error=>{ return error.response })

  return res;
}

abono.get = async (id) => {
  const urlGet = baseUrl+"find/"+id
  const res = await axios.get(urlGet)
  .then(response=> {return response.data })
  .catch(error=>{ return error; })
  return res;
}
abono.getUltimo = async ()=>{
  const urlGetUltimo= baseUrl+"ultimo";
  const resp= await axios.get(urlGetUltimo)
  .then(response=>{return response.data})
  .catch(error=>{return error})
  return resp
}


abono.list = async () => {
  const urlList = baseUrl+"/all"
  const res = await axios.get(urlList)
  .then(response=> {return response.data })
  .catch(error=>{ return error; })
  return res;
}
abono.imagen = async (id,imag)=>{
  const urlImagen= baseUrl+"imagen/"+id
  console.log(imag)
  const res= await fetch(urlImagen,{
    method:'post',
    body:imag


  })
  
  .then(response=>{
		const data = { success: true, message: response.data }
		return data;
	})
	.catch(error=>{
		const data = { success: false, message: error.response.data }
		return data;
	})

  return res;
}
abono.create = async (state) => {

	const datapost = {
    legajo: state.legajo,
    nombre: state.nombre,
    apellido: state.apellido,
    especialidad: state.especialidad,
    fechaIngreso: state.fechaIngreso,
    fecha_nacimiento: state.fecha_nacimiento,
    idEscuela: state.idEscuela

  }

	const urlPost = baseUrl+"/create"
  
  console.log(datapost)
	const res = await axios.post(urlPost,datapost)
	.then(response=>{
		const data = { success: true, message: response.data }
		return data;
	})
	.catch(error=>{
		const data = { success: false, message: error.response.data }
		return data;
	})

  return res;
  
}

export default abono