import React, { Component } from "react";
import { Button, Form } from "react-bootstrap";
import SignaturePad from "react-signature-canvas";
import   './Firma1.css'

class Firma1 extends Component {
    
    state = {trimmedDataURL: null}
    sigPad = {}
    clear = () => {
      this.sigPad.clear()
    }
    trim = () => {
      this.setState({trimmedDataURL: this.sigPad.getTrimmedCanvas()
        .toDataURL('image/png')},()=>{this.props.callback(this.state.trimmedDataURL)
        console.log(this.state.trimmedDataURL)
        })
      

            
            
    }
  
    
    
 
    
    render () {
      let {trimmedDataURL} = this.state

      
      return <div >
        
         
          <SignaturePad canvasProps={{class:"signCanvas"}}
            ref={(ref) => { this.sigPad = ref }} 
            />
        
        
          <Button onClick={this.clear}>
            Limpiar
          </Button>
          <Button  onClick={this.trim}>
            Guardar
          </Button>
        
        {trimmedDataURL
          ? <img 
            src={trimmedDataURL}
            style={{
                display: "block",
                margin: "0 auto",
                border: "1px solid black",
                width: "150px"
              }}
            />
          : null}
      </div>
    }
  }
  export  default  Firma1