import React , {useRef} from "react";

import routes from "routes.js";
import Busqueda from "../components/Navbars/Busqueda";
import empresaServices from "../components/service/Empresa"
import Firma1 from "../components/Firma1";
import tecnicoServices from "../components/service/Tecnico"
import { Modal,
  ModalTitle,
  ModalHeader,
  ModalBody,
  ModalFooter, } from "react-bootstrap";
// reactstrap components
import {
  Card,
  CardHeader,
  CardBody,
  CardTitle,
  Table,
  Row,
  Col,
  Button, 
  //Modal,
  
  Input,
  FormGroup
} from "reactstrap";
import abonoServices from "../components/service/OrdenServicio"
import SignaturePad from 'react-signature-canvas';
import ReactSignatureCanvas from "react-signature-canvas";
import { isAwaitExpression, ScriptElementKindModifier } from "typescript";


class TablesAbono extends React.Component {
    constructor(props)
  {
    super(props)
    this.setModalEditar=this.setModalEditar.bind(this);
    this.setAbono=this.setAbono.bind(this);
    this.setIdDelete=this.setIdDelete.bind(this);
    this.valorSearchEmpresa=this.valorSearchEmpresa.bind(this);
    this.setModalCrear=this.setModalCrear.bind(this);
     this.setEmpresa=this.setEmpresa.bind(this);
    this.setTecnico=this.setTecnico.bind(this);
    this.state = {
        listAbono:[],
        listBusquedaAbono:[],
        listEmpresa:[],
        ListaAbono:[],
        listTecnico:[],
        id: 0,
        legajo: '',
        nombre: "",
        apellido: "",
        especialidad: "",
        fechaIngreso: "",
        fecha_nacimiento: "",
        idEscuela: {
        idEscuela: 0,
        nombre: ""
        },
          
          modalEditar:false,
          modalEliminar:false,
          editarModal:true,
          modalCrear:false
    }
    
  }
  getResponse(e){
    this.setState({firmadata:e},()=>{console.log(this.state.firmadata)});
    
}
   async setEmpresa(e){

    const id=e.target.value
    console.log(id);
     const elemento = await empresaServices.get(id)
    console.log(elemento);
    if (elemento.success) {
        this.setState(
        {
          idEscuela:elemento.data
        }
        )
        console.log(this.state.idEscuela)
    }
    else {
      alert("Error server ==>"+JSON.stringify(elemento))
    }
   
  }
  async setTecnico(e){

    const id=e.target.value
    console.log(id);
     const elemento = await tecnicoServices.get(id)
    console.log(elemento);
    if (elemento.success) {
        this.setState(
        {
          idTecnico:elemento.data
        }
        )
        console.log(this.state.idTecnico)
    }
    else {
      alert("Error server ==>"+JSON.stringify(elemento))
    }
   
  }

    setAbono(elemento,caso){
           if (caso == 'Editar'){
                this.setState({editarModal:false})
            }else{
                this.setState({editarModal:true})
            }
    this.setState((state) => ({
          id: elemento.id,
          legajo: elemento.legajo,
          nombre: elemento.nombre,
          apellido: elemento.apellido,
          especialidad: elemento.especialidad,
          fechaIngreso: elemento.fechaIngreso,
          fecha_nacimiento: elemento.fecha_nacimiento,
          idEscuela: {
          idEscuela: elemento.idEscuela.idEscuela,
          nombre: elemento.idEscuela.nombre
          },
          ie:'',
          ide:'',
          busqueda:'',
          modalEditar:true
    }))
     
        
 }

 setModalCrear(){
     this.setState({
          id: '',
          legajo:'',
          nombre: '',
          apellido: '',
          especialidad: '',
          fechaIngreso: '',
          fecha_nacimiento: '',
          idEscuela: {
          idEscuela: '',
          nombre: ''
          },
          busqueda:"",
          modalCrear:!this.state.modalCrear,
    });

   }

  setModalEliminar(){
    this.setState({modalEliminar:!this.state.modalEliminar})
  }
  
  setIdDelete(i,id){
      this.setState({
        ie:i,
        ide:id,
        modalEliminar:true
      })
  }

  async resetLista(){

    const res = await abonoServices.list()
    console.log(res);
    if (res.success) {
       this.setState({listAbono:res.list})
       this.filtrarElementos();
    }
    else {
      alert("Error server ==>"+JSON.stringify(res))
    }

  }

  async onClickSave()
  {
    const res = await abonoServices.create(this.state)
    if (res.success) {
      
     
      alert(res.message)
      this.setState({modalCrear:false});
      
    }
    else {
      alert("Error ==>"+res.message.message)
    }
    this.resetLista();
      this.filtrarElementos();
  }



  async onClickDelete()
  {
    const id=this.state.ide;
    const i=this.state.ie;
    var yes = true;
    console.log(id,i);
    if (yes === true){ 
      
      const res = await abonoServices.delete(id)

      if (res.success) {
        this.resetLista();
        const list =this.state.listBusquedaAbono
        list.splice(i,1)
         this.setState((state)=>({listBusquedaAbono:list,
                      modalEliminar:false
        }))
      }
      else{
        console.log(res); alert(JSON.stringify(res))
      } 
    }
  }

  setModalEditar(){

    this.setState({
        modalEditar:!this.state.modalEditar
    })
  };

    async onClickUpdate()
  { 
    console.log("Execute update");
    console.log(this.state.id);
    const res = await abonoServices.update(this.state)
    if (res.success) {
      console.log(this.state.id)
      alert(res.message)
     this.setState({modalEditar:false});
     this.resetLista();
      this.filtrarElementos();
    }
    else {
      console.log("Error");
      console.log(res);
      alert("Error ==>"+JSON.stringify(res.data))
      this.setState({modalEditar:false});
    }

  }
  
 async valorSearchEmpresa(emp){
  console.log(emp)
  await this.setState({
    busqueda:emp
  })
  this.filtrarElementos();
 }

 filtrarElementos=()=>{
  const lista=this.state.listAbono;
  var search=lista.filter(item=>{
    if(item.id.toString().includes(this.state.busqueda)||
       item.idEscuela.nombre.toLowerCase().includes(this.state.busqueda)||
       item.idEscuela.nombre.includes(this.state.busqueda)||
       item.nombre.toLowerCase().includes(this.state.busqueda)||
       item.nombre.includes(this.state.busqueda)||
       item.apellido.includes(this.state.busqueda)||
       item.apellido.toLowerCase().includes(this.state.busqueda)
       )
  
            {
              return item
            }
  })
    console.log(search)
    this.setState({listBusquedaAbono:search})
 }
 
  async componentDidMount(){
    
    
    //this.setState({listBusquedaAbono:this.state.listAbono});
    console.log("Mounted List");
    const res = await abonoServices.list()
    console.log(res);
    if (res.success) {
      this.setState({listAbono:res.list})
    }
    else {
      alert("Error server ==>"+JSON.stringify(res))
    }

  

     console.log("Mounted ListEmpresa");
    const res1 = await empresaServices.list()
    console.log(res1);
    if (res1.success) {
      this.setState({listEmpresa:res1.list});
      this.setState({listBusquedaAbono:this.state.listAbono});
    }
    else {
      alert("Error server ==>"+JSON.stringify(res1))
    }

  
    
 
  }

  
//

  render() {
   
      const editarModal=this.state.editarModal;
       const nombreEscuela=this.state.idEscuela.nombre;
      //const nombreTecnico=this.state.idTecnico.nombre;
      const URLactual = window.location.host;
      //const baseUrl ="http://"+URLactual+"/api/abono/pdfReport/";
  
    return (
      <>
        
       
                    
        <div className="content">
          <Row>
            <Col md="12">
            <div class="row ">
                    <div class="form-group col-md-6 ">
            <Busqueda searchBusqueda={this.valorSearchEmpresa}/>
                    </div>
           <div class="form-group col-md-6 pl-5 ">
            <Button color="success"  onClick={this.setModalCrear}  size="lg">Nuevo</Button>
            </div>
            </div>
              <Card>
                <CardHeader>
                  <CardTitle tag="h4">Profesor</CardTitle>
                </CardHeader>
                <CardBody>
                  <Table className="tablesorter" responsive>
                    <thead className="text-primary">
                      <tr>
                        <th>Nombre Profesor</th>
                        <th>Apellido</th>
                        <th>Escuela</th>
                        
                       
                      </tr>
                    </thead>
                    <tbody>
                      {
              this.state.listBusquedaAbono.map((data,i)=>{
                return(
                  <tr>
                    <th scope="row">{data.nombre}</th>
                    <th>{data.apellido}</th>
                    <td>{data.idEscuela.nombre}</td>
                   
                    
                    <td>
                           <Button className="btn-icon" color="info" size="sm"
                           onClick={()=>{this.setAbono(data,'Ver')}} 
                        href="#">
                          <i className="fas fa-eye" />
                          </Button>{` `}
                           
                         <Button  className="btn-icon" color="success" size="sm"
                          onClick={()=>{this.setAbono(data,'Editar')}} 
                         >
                          <i className="fas fa-pencil-alt"></i>
                         </Button>{` `}

                          <Button className="btn-icon" color="primary" size="sm" 
                        href="#" onClick={()=>this.setIdDelete(i,data.id)}>
                          <i className="fas fa-trash-alt" />
                          </Button>{'  '}

                          {/* <Button className="btn-icon" color="danger" size="sm" 
                            href={baseUrl+data.id} >
                            <i class="fas fa-file-pdf"></i>
                            </Button>
                */}
                    </td>
                  </tr>
                )
              })
            }
                    </tbody>
                  </Table>
                </CardBody>
              </Card>
            </Col>
            
          </Row>
       
          </div>
           <Modal  show={this.state.modalEditar} onHide={this.setModalEditar} animation={false}
           size="lg"  backdrop="static" scrollable={true}>
                <Modal.Header closeButton>
          <Modal.Title id="example-custom-modal-styling-title">
           Profesor
          </Modal.Title>
        </Modal.Header>
                <Modal.Body scrollable={true} >
                
                <div class="row ">
                    
                    <div class="form-group col-md-4 ">
                     <label>ID Profesor</label>
                      <input
                        className="form-control bg-white text-dark "
                        readOnly={editarModal}
                        type="text"
                        name="id"
                        value={this.state.id}
                        onChange={(value)=>this.setState({id:value.target.value})}
                      /> 
                      </div> 
                      <div class="form-group col-md-4 ">
                      <label>Escuela</label>
                      <input
                        className="form-control bg-white text-dark "
                        readOnly={editarModal}
                        type="text"
                        name="escuela"
                        value={this.state.idEscuela.nombre}
                        onChange={(value)=>this.setState({idEscuela:{nombre:value.target.value}})}
                      /> 
                      </div>
                    </div> 

                    <div class="border border-info my-2 p-2"> 
                    <h3 class="bg-white text-dark" >Datos del Profesor </h3>
                    <div class="row ">
                    <div class="form-group col-md-4  ">
                     <label>Legajo</label>
                      <input
                        className="form-control bg-white text-dark "
                        readOnly={editarModal}
                        type="text"
                        name="legajo"
                        value={this.state.legajo}
                        onChange={(value)=>this.setState({legajo:value.target.value})}
                      /> 
                      </div> 
                    <div class="form-group col-md-4 ">
                     <label>Nombre</label>
                      <input
                        className="form-control bg-white text-dark "
                        readOnly={editarModal}
                        type="text"
                        name="nombre"
                        value={this.state.nombre}
                        onChange={(value)=>this.setState({nombre:value.target.value})}
                      /> 
                      </div> 
                      <div class="form-group col-md-4 ">
                      <label>Apellido</label>
                      <input
                        className="form-control bg-white text-dark"
                        readOnly={editarModal}
                        type="text"
                        name="apellido"
                        value={this.state.apellido}
                        onChange={(value)=>this.setState({apellido:value.target.value})}
                      /> 
                      </div>
                    </div> 

                    <div class="row ">
                    <div class="form-group col-md-4 ">
                     <label>Especialidad</label>
                      <input
                        className="form-control bg-white text-dark "
                        readOnly={editarModal}
                        type="text"
                        name="especialidad"
                        value={this.state.especialidad}
                        onChange={(value)=>this.setState({especialidad:value.target.value})}
                        valid={true}
                      /> 
                      </div> 
                    <div class="form-group col-md-4 ">
                     <label>Fecha Ingreso</label>
                      <input
                        className="form-control bg-white text-dark"
                        readOnly={editarModal}
                        type="date"
                        name="fechaIngreso"
                        value={this.state.fechaIngreso}
                        onChange={(value)=>this.setState({fechaIngresoo:value.target.value})}
                      /> 
                      </div> 
                      <div class="form-group col-md-4 ">
                      <label>Fecha Nacimiento</label>
                      <input
                        className="form-control bg-white text-dark"
                        readOnly={editarModal}
                        type="date"
                        name="fecha_nacimiento"
                        value={this.state.fecha_nacimiento}
                        onChange={(value)=>this.setState({fecha_nacimiento:value.target.value})}
                      /> 
                      </div>
                    </div> 
</div>
                    
                </Modal.Body>
                <Modal.Footer>
                    <Button color="secondary" onClick={this.setModalEditar}>
                        Cerrar
                    </Button>
                    <Button color="warning" disabled={editarModal} 
                    onClick={()=>this.onClickUpdate()}>
                        Update
                    </Button>
                   
                </Modal.Footer>
            </Modal> 
            <Modal show={this.state.modalEliminar} onHide={this.setModalEliminar} backdrop="static" >
                <Modal.Body>
                  Estás Seguro que deseas eliminar Profesor
                </Modal.Body>
                <Modal.Footer>
                  <button className="btn btn-danger" onClick={()=>this.onClickDelete()}>
                    Sí
                  </button>
                  <button
                    className="btn btn-secondary"
                    onClick={()=>{this.setState({modalEliminar:false})}}
                  >
                    No
                  </button>
                </Modal.Footer>
              </Modal>

               <Modal  show={this.state.modalCrear} onHide={this.setModalCrear} 
               size="lg" backdrop="static" animation={false} >
                      <Modal.Header closeButton>
          <Modal.Title id="example">
           Profesor
          </Modal.Title>
        </Modal.Header>
                
        <Modal.Body scrollable={true} >
                
                <div class="row ">
                    
                     
                      <div class="form-group col-md-4 ">
                      <label>Escuela</label>
                      <Input
                        className="form-control form-control bg-white text-dark  "
                        
                        type="select"
                        name="empresa"
                        value={nombreEscuela}
                        onChange={this.setEmpresa}
                      > 
                      <option value ="">{nombreEscuela}</option>
                       {this.state.listEmpresa.map((data,i)=>{
                              return(
                                
                                <option value={data.idEscuela}>{data.nombre}</option>
                            ); 
                              }
                        )}
                      </Input>
                      </div>
                    </div> 

                    <div class="border border-info my-2 p-2"> 
                    <h3 class="bg-white text-dark" >Datos del Profesor </h3>
                    <div class="row ">
                    <div class="form-group col-md-4  ">
                     <label>Legajo</label>
                      <input
                        className="form-control bg-white text-dark "
                        
                        type="text"
                        name="legajo"
                        value={this.state.legajo}
                        onChange={(value)=>this.setState({legajo:value.target.value})}
                      /> 
                      </div> 
                    <div class="form-group col-md-4 ">
                     <label>Nombre</label>
                      <input
                        className="form-control bg-white text-dark "
                     
                        type="text"
                        name="nombre"
                        value={this.state.nombre}
                        onChange={(value)=>this.setState({nombre:value.target.value})}
                      /> 
                      </div> 
                      <div class="form-group col-md-4 ">
                      <label>Apellido</label>
                      <input
                        className="form-control bg-white text-dark"
                      
                        type="text"
                        name="apellido"
                        value={this.state.apellido}
                        onChange={(value)=>this.setState({apellido:value.target.value})}
                      /> 
                      </div>
                    </div> 

                    <div class="row ">
                    <div class="form-group col-md-4 ">
                     <label>Especialidad</label>
                      <input
                        className="form-control bg-white text-dark "
                       
                        type="text"
                        name="especialidad"
                        value={this.state.especialidad}
                        onChange={(value)=>this.setState({especialidad:value.target.value})}
                        valid={true}
                      /> 
                      </div> 
                    <div class="form-group col-md-4 ">
                     <label>Fecha Ingreso</label>
                      <input
                        className="form-control bg-white text-dark"
                       
                        type="date"
                        name="fechaIngreso"
                        value={this.state.fechaIngreso}
                        onChange={(value)=>this.setState({fechaIngreso:value.target.value})}
                      /> 
                      </div> 
                      <div class="form-group col-md-4 ">
                      <label>Fecha Nacimiento</label>
                      <input
                        className="form-control bg-white text-dark"
                      
                        type="date"
                        name="fecha_nacimiento"
                        value={this.state.fecha_nacimiento}
                        onChange={(value)=>this.setState({fecha_nacimiento:value.target.value})}
                      /> 
                      </div>
                    </div> 
</div>
                    
                </Modal.Body>
                    
                
                <Modal.Footer>
                     <Button color="secondary" onClick={this.setModalCrear}>
                        Cerrar
                    </Button>
                    <Button color="warning" 
                    onClick={()=>this.onClickSave()}>
                        Crear
                    </Button>
                   
                </Modal.Footer>
            </Modal>

            
      </>
    )
  }

  



  
  
  

  
   
}

export default TablesAbono;
